::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  position: absolute;
}

/* Track */
::-webkit-scrollbar-track {
  background: #e6e6e6;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(132, 255, 198, 0.719);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(116, 224, 174, 0.719);
}

